//引入vue
import Vue from 'vue'
import cookie from './static/js/cookie'
import VueRouter from 'vue-router'
import axios from 'axios';
import VueAxios from 'vue-axios'


Vue.use(VueRouter)
Vue.use(VueAxios, axios)

const router = new VueRouter({
    routes: [

      {
        path: '/tag',
        component: resolve => require(['./page/tag/tag.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/interns',
        component: resolve => require(['./page/tag/student-list.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/tag/:id',
        component: resolve => require(['./page/tag/tag_detail.vue'], resolve) /* <== 主要代码是这 */
      },

    
      {
        path: '/student/:id',
        component: resolve => require(['./page/tag/student.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/',
        component: resolve => require(['./page/home/home.vue'], resolve) /* <== 主要代码是这 */
  
      },


      
        {
            path: '/login',
            component: resolve => require(['./page/login/login.vue'], resolve) /* <== 主要代码是这 */
        },
        {
            path: '/register',
            component: resolve => require(['./page/register/register.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/setting',
          component: resolve => require(['./page/setting/setting.vue'], resolve), /* <== 主要代码是这 */
          meta: {
            title: 'My setting',
            // requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path: '/profile',
          component: resolve => require(['./page/setting/userprofile.vue'], resolve), /* <== 主要代码是这 */
          meta: {
            title: 'My Profile',
            // requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path: '/account',
          component: resolve => require(['./page/setting/account.vue'], resolve), /* <== 主要代码是这 */
          meta: {
            title: 'My account',
            // requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path: '/roomie',
          component: resolve => require(['./page/roomie/roomie.vue'], resolve), /* <== 主要代码是这 */
        
        },
        {
          path: '/roomie-room',
          component: resolve => require(['./page/roomie/roomwithroomie.vue'], resolve), /* <== 主要代码是这 */
        
        },
        {
          path: '/search',
          component: resolve => require(['./page/search/search.vue'], resolve), /* <== 主要代码是这 */
        
        },
        {
          path: '/saved',
          component: resolve => require(['./page/saved/saved.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/saved-apartments',
          component: resolve => require(['./page/saved/apartments.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/saved-roomie',
          component: resolve => require(['./page/saved/roomie.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/newskill',
          component: resolve => require(['./page/skills/newskill.vue'], resolve),
          meta: {
            title: 'upload my place',
            // requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path: '/newproject',
          component: resolve => require(['./page/skills/newProject.vue'], resolve),
          meta: {
            title: 'upload my place',
            // requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path: '/skills',
          component: resolve => require(['./page/skills/skills.vue'], resolve),
          meta: {
            title: 'upload my place',
            // requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
          }
        },
        {
          path: '/teach',
          component: resolve => require(['./page/teach/teach_list.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/review',
          component: resolve => require(['./page/review/review.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/shared',
          props: true,
          component: resolve => require(['./page/listing/shared.vue'], resolve) /* <== 主要代码是这 */
        },
        {
          path: '/newfamily',
          props: true,
          component: resolve => require(['./page/setting/newfamily.vue'], resolve) /* <== 主要代码是这 */
        },
        {
            path: '/details/:id',
            props: true,
            component: resolve => require(['./page/listing/details.vue'], resolve) /* <== 主要代码是这 */
        }
    ]
  });


  router.beforeEach((to, from, next) => {
    // 设置标题
    // document.title = to.meta.title
    // store.commit('UPDATE_TITLE', to.meta.title)
    // loading
    // store.commit('UPDATE_LOADING', true)
  // 这里设置如果要求登录的地方就跳到登录页面
    if (to.matched.some(r => r.meta.requireAuth)) {
      // console.log(cookie.getCookie('token'))
        if (cookie.getCookie('token')) {
          console.log('有cookie')
            next()
        } else {
          console.log('没有cookie')
            next({
                path: '/login',
                query: { redirect: to.fullPath }
            })
        }
    } else {
        next()
    }
  })


  axios.interceptors.request.use(
    config => {
      if (cookie.getCookie('token')) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
        config.headers.Authorization = `JWT ${cookie.getCookie('token')}`;
      }
      return config;
    },
    err => {
      return Promise.reject(err);
    });

  export default router


