import * as types from './mutation-types'

const mutations = {
  // [types.SET_SINGER](state, singer) {
  //   state.singer = singer
  // },
  // [types.SET_PLAYING_STATE](state, flag) {
  //   state.playing = flag
  // }
  addroomimg(state,img) {
    state.roomimg.push(img)
  },
  deleteroomimg(state,img) {
    state.roomimg.pop(img)
  },
  adduserimg(state,img){
    state.userimg.push(img)
  },
  deleteuserimg(state,img){
    state.userimg.pop(img)
  }
}

export default mutations


